@import url(https://fonts.googleapis.com/css2?family=Merienda&display=swap);
body > #root > div {
  height: 100vh;
}

body {
  background-color: #e6e6e6;
}

.top-row {
  background-size: cover;
  height: 15vh;
  margin-bottom: 10vh;
  justify-content: center;
  background-position: center; 

}

.top-row h1 {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin: 0px;
  text-align: center;
  color: #fff;
  font-family: 'Merienda', cursive;
  text-shadow: 0px 0px 5px black;
}

.home-top-row {
  position: relative;
  top: 0vh;
  text-align: center;
  font-size: 2.2rem;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  height: 10vh;
  text-shadow: 0px 0px 0px rgba(68, 35, 95, 0.459);
}

.div-home {
  margin: 0px;
  padding: 0px;
  width: 100vw;
  height: 100vh;
}

.gh-icon {
  margin: 20px 0px 0px 20px;
  transform: scale(1.6);
}

.home {
  padding: 0px;
  height: 100vh;
  overflow: hidden;
  z-index: 1;
  font-family: 'Merienda', cursive;
  color: #391D82;
  font-size: 1.1rem;
  background-image: url('https://images.unsplash.com/photo-1534592302589-d4f8f14c6f47?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1956&q=80');
}

.home p, .shop-now-btn {
  position: absolute;
  right: 5%;
  top: 34%;
  text-align: center;
  justify-content: center;
}

.shop-now-btn {
  right: 16%;
  top: 47%;
  border: none;
  outline: none;
  background-image: linear-gradient(to right, #AF83E3 0%, #391D82  51%, #AF83E3  100%);
  margin-top: 15px;
  padding: 15px 45px;
  transition: 0.5s;
  background-size: 200% auto;
  color: white;            
  box-shadow: 0 0 20px #eee;
  border-radius: 10px;
  display: block;
  cursor: pointer;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-family: inherit;
}

.shop-now-btn:hover {
  background-position: right center; /* changes the direction of the change here */
  color: #fff;
  text-decoration: none;
}

.home-image {
  background-size: cover;
  width: 90vw;
  height: 10vw;
  padding: 0%;
  margin: auto;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about h2 {
  padding-top: 10vh;
  font-family: 'Merienda', cursive;
}

.about-img-wrapper {
  border: 0px solid;
  border-radius: 500px;
  overflow: hidden;
  background-image: url('https://images.unsplash.com/photo-1551947391-249dcb8ed976?ixlib=rb-1.2.1&ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&auto=format&fit=crop&w=967&q=80');

  width: 200px;
  height: 200px;
  background-size: cover;
  background-position: center;
}

.item-add-to-cart {
  font-size: 1.4rem;
}

.add-rem {
  border: 2px solid rgb(255, 255, 255);
  cursor: pointer;
  padding: 4px 16px 4px 10px;
  width: 20px;
  background-color: rgb(175, 175, 175);
  color: rgb(255, 255, 255);
  justify-content: center;
  text-align: right;
  outline: none;
  border-radius: 5px;
}

.item-add-to-cart input {
  cursor: pointer;
  background:  none;
  border: none;
  font-size: 1.4rem;
  outline:  none;
  color: rgb(105, 105, 105);
  width: 30px;
  height: 30px;
  margin-right: 10px;
  margin-left: 20px;

}

.add-to-cart-button, .finalize-btn {
  margin-left: 20px;
  border: 2px solid white;
  cursor: pointer;
  width: 100px;
  height: 30px;
  background-color: chocolate;
  color: #fff;
  border-radius: 5px;
  outline: none;
}

.add-rem:hover,  
.add-to-cart-button:hover,
.finalize-btn:hover  {
  background-color: saddlebrown;
  transform: scale(1.05);
}

.add-rem:active,  
.add-to-cart-button:active,
.finalize-btn:active  {
  background-color: rgb(0, 0, 0);
  transform: scale(0.95);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
.navbar, .navbar-header, .navbar-nav {
  background-color: rgb(31, 19, 5);
  color: white;
  height: 7vh;
  z-index: 5;
}

.navbar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-header div {
  padding: 10px;
  font-size: 1.5rem;
}

.fa-icon {
  margin-left: 8px;
  width: 40px;
  height: 40px;
  padding-right: 7px;
  color: #e9891b;
}

.right-elements {
  display: inline;
}

.store-logo {
  font-family: 'Merienda', cursive;

}

.cart-li, .store-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px 5px !important;
}

.nav-links li {
  padding: 5px 10px;
  margin: 5px 10px;
  align-items: center;
  justify-content: center;
}

.nav-links a:hover {
  background-color: rgba(255, 255, 255, 0.13);
}

.nav-links {
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.item-quantity {
  background-color: rgb(131, 58, 6);
  border: 2px;
  border-radius: 50px;
  font-size: 0.8rem !important;
  height: 20px;
  width: 20px;
  padding: 0px !important;
  text-align: center;
  vertical-align: middle;
  line-height: 20px;
  font-weight: 600;
  margin-left: -12px;
  margin-top: 19px;
}

.store-logo {
  justify-items: center;
  justify-content: center;
  justify-self: center;
}
.store-div {
  display: flex;
  justify-content: center;
  align-content: center;
}

.items-display {
  display: flex;
  width: 80vw;
  flex-wrap: wrap;
  margin: auto;
}

.store-item {
  width: 250px;
  margin: 20px;
  display: flex;
  flex-direction: column;
  word-wrap: break-word;
}

.store-item p {
  text-decoration: none;
  word-wrap: break-word;
  margin: 3px;
}

.items-display img {
  width: 250px;
}

.item-origin {
  font-style: italic;
  /* font-size: 0.9rem; */
}

.item-price {
  color: rgb(75, 65, 9);
  margin: 0px;
  margin-bottom: 20px;
}


.item-div {
  display: flex;
  justify-content: space-evenly;
}

.item-display {
  width: 50vw;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.image-display img {
  width: 35vw;
}

 p {
  margin: 5px;
}

.item-description {
  font-style: italic;
  margin-bottom: 20px;
}

.item-display h5 {
  margin: 5px;
  font-size: 1.5rem;
  color: darkgray ;
}

.origin {
  color: rgb(75, 65, 9);
  margin-bottom: 20px;
}

.item-add-to-cart {
  margin-top: 5vh;
}
.cart-div {
  width: 80vw;
  border: 2px solid rgb(109, 109, 109);
  border-radius: 10px;
  margin: auto;
  margin-bottom: 10vh;
}

.cart-title {
  text-align: center;
  padding: 40px;
}

.cart-table {
  align-items: center;
  margin-bottom: 5vh;
}

.cart-table li {
  display: flex;
  margin: 20px;
  text-align: center;
  align-items: center;
  border: 1px solid rgb(202, 202, 202);
  overflow: auto;
}

.cart-table li::marker {
  padding-right: 30px;
  font-size: 1.2rem;
}

.cart-table li img {
  width: 80px;
}

a span {
  padding: 20px;
}

.cart-table li p {
  padding-right: 30px;
  margin: 0px;
}

.cart-item-menage {
  position: absolute;
  right: 15vw;
  display: flex;
  justify-content: space-between;
  width: 20vw;
  align-items: center;
  justify-items: center;
}

.trash-del {
  transform: scale(1.2);
  background: none;
  border: none;
  outline: none;
  color: saddlebrown;
}

.trash-del:hover {
  color: rgb(255, 106, 0);
  transform: scale(1.4);
 }

 .total {
   margin-left: 50vw;
   padding-bottom: 5vh 0px 5vh 0px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 23vw;
   font-size: 1.1rem;
  }

 .finalize-btn {
   width: 120px;
   height: 40px;
   font-size: 1.1rem;
  } 
  
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

